<template>
    <div class="content-box">
        <div class="title-box">
            <p class="header-line"><i class="fa fa-cogs m-r-10"></i>{{$t('setting.setting')}}</p>
        </div>
       
        <div class="select-box">
            <mode-switch @mode-status="changeMode" ></mode-switch>
            <select-group 
            @select-getDeviceOptions="getGroupOption" 
            @select-getGroupName="getGroupName" 
            @groupType="setGroupType" 
            :isSingleMode="isSingleMode">
            </select-group>
            <select-device 
            @select-device="getDeviceOption"
            @isOfflineDev="isOfflineDev" 
            :isSingleMode="isSingleMode"
            @deviceType="setDeviceType"
            :isDisabledOffDev="isDisabledOffDev">
            </select-device>
        </div>
        <div>
           <single-control 
            :selectedAgentId="selectedAgentId" 
            :isSingleMode="isSingleMode"
            @isDisabled="isDisabled"
            :deviceType="deviceType"
            :isOffline="isOffline">
            </single-control>
            <batch-control 
            :selectedAgentsData="selectedAgentsData" 
            :isSingleMode="isSingleMode"
            :groupType="groupType"
            :groupname="groupname"
            ></batch-control>
        </div>
    </div>  
</template>
<script>
    import selectDevice from '../../common/select-device'
    import singleControl from "./settingdetails/single"
    import batchControl from "./settingdetails/batch"
    import modeSwitch from "../../common/mode-switch"
    import selectGroup from "../../common/select-group"
    import {getDeviceStatus} from "../restfulapi/pluginsApi"
    import handleResponse from "../restfulapi/handleResponse"
    import { parseGeoJSON } from 'echarts/lib/export'
    import {groupType} from "../../assets/js/constant"

    export default {
        name: 'settingsetting',
        data() {
            return {
            selectedAgentId: '',
            selectedAgentsData: [],
            isSingleMode: true,
            groupname: '',
            //offline device is disabled
            isDisabledOffDev: true,
            //selectedAgent is offline
            isOffline: false,
            deviceType: "",
            groupType: "",
            groupTypeArray: [groupType.android, groupType.linux, groupType.windows],
            groupTypes: [groupType.android, groupType.linux, groupType.windows]
        }
    },
    components:{
        selectDevice,
        selectGroup,
        modeSwitch,
        singleControl,
        batchControl
    },
    methods: {
        getDeviceOption(selectedAgentId){ 
            this.selectedAgentId = selectedAgentId;
        },

        getGroupOption(selectedAgentsData){
            this.selectedAgentsData = selectedAgentsData;
        },

        initData(){
            this.selectedAgentId = "";
            this.groupname = "";
            this.selectedAgentsData = [];
        },

        changeMode(val){
            this.initData();
            this.isSingleMode = !val
        },

        getGroupName(groupname){
            this.groupname = groupname;
        },

        isDisabled(value){
            this.isDisabledOffDev = value;
        },
        isOfflineDev(value){
            this.isOffline = value
        },

        setDeviceType(val){
            this.deviceType = val;
        },
        setGroupType(val){
            this.groupType = val;
        }
        
    },
}
</script>

<style lang="scss">
@import "../../assets/css/colors";
    .note{
        padding: 10px;
        color: $darkgray-color;
        font-size:14px;
        i{
            color: $danger-color;
            margin-right: 8px;
            
        }
    }
</style>



