<script>
    import {getDeviceStatus, setDeviceStatus, execDeviceStatus} from '../../restfulapi/pluginsApi'
    import selectDevice from '../../../common/select-device'
    import {androidControl, actionDevice} from '@/assets/js/lwm2mMap'
    import handleResponse from '../../restfulapi/handleResponse'
    import selectGroup from "../../../common/select-group"
    import {number} from 'echarts/lib/export'
    import cardTemp from "../../../common/cardTemp"
    import {appControl, dockerAction} from '@/assets/js/lwm2mMap'
    import {showSystemAppArray, disableAppArray, pluginname, groupType} from "@/assets/js/constant"
    import {getDeviceStatus as getDeviceStatusAsByTarget, setDeviceStatus as setDeviceStatusByTarget} from '../../restfulapi/deviceStatusApi'
    import {getRepoSrcApi} from "../../restfulapi/serverConfigApi"
    import {getRepoAppsApi} from '@/components/restfulapi/getRepoAppApi';

    export default{
        name: 'singleSetting',
        data(){
            return {
                activeName: "thresholdSetup",
                cputem: false,
                cputemswitch: false,
                cputemwarn: 0,
                cputemerror: 0,
                cpuusage: false,
                cpuusageswitch: false,
                cpuusagewarn: 0,
                cpuusageerror: 0,
                memoryusage: false,
                memoryusageswitch: false,
                memoryusagewarn: 0,
                memoryusageerror: 0,
                storageusage: false,
                storageusageswitch: false,
                storageusagewarn: 0,
                storageusageerror: 0,
                battery: false,
                batteryswitch: false,
                batterywarn: 0,
                batteryerror: 0,
                appTableData: [],
                defaultImg: 'this.src="' + 'static/imgs/default_apk.png' + '"',
                appType: "third",
                appOptions: [],
                appInMonitor: [],
                groupTypeArray: groupType,
                processData: [],
                monitorProcessData: [],
                multipleSelection: [],
                multipleContainerSelection: [],
                softwareLoading: false,
                keywords: "",
                monitorType: "addMonitor",
                monitorContainerType: "addMonitor",
                containerData: [],
                monitorContainerData: [],
                dockerIconColor: "#626369",
                isFixed: false,
                offsetTop: 10000            
                }
        },

        props: {
            selectedAgentId: {
                type: String,
                default: ""
            },
            isSingleMode: {
                type: Boolean,
                default: true
            },
            //selectedAgent is offline
            isOffline: {
                type: Boolean,
                default: false
            },

            deviceType: {
                type: String,
                default: ""
            }
        },
        components: {
            selectDevice,
            selectGroup,
            cardTemp,
        },

        methods:{
            tabEnter(){
                this.dockerIconColor = "#409EFF";
            },
            tabLeave(){
                if(this.activeName != "dockerMonitor"){
                    this.dockerIconColor = "#626369";
                }
            },
            selectItem(val) {
                this.multipleSelection = val;
            },
            selectDockerItem(val){
                this.multipleContainerSelection = val;
            },
            getDockerStatus(){
                if(!this.selectedAgentId) {
                    console.error("agentId is empty");
                    return;
                }
                let param = "{'get_docker_info':true}"
                this.softwareLoading = true;
                getDeviceStatus(this.selectedAgentId, param).then((obj) => {
                    this.softwareLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            let dockerInfo = JSON.parse(res.content.value);
                            let monitorContainerData = [];
                            let containerData = [];
                            dockerInfo.forEach((item) => {
                                if(item.ismonitor === 'true'){
                                    monitorContainerData.push(item);
                                }else{
                                    containerData.push(item);
                                }
                            })
                            this.containerData = containerData;
                            this.monitorContainerData = monitorContainerData;
                        }
                    })   
                }) 
            },

	        getRepoApps(deviceAppArray){
                if(Object.prototype.toString.call(deviceAppArray) != "[object Array]"){
                    console.error("getRepoApps　parameter is not array")
                    return;
                }
                this.whitelistLoading = true;
                getRepoAppsApi().then((data) => {
                    this.whitelistLoading = false;
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                res.data.forEach((val) => {
                                    deviceAppArray.forEach((deviceapp_val, index) => {
                                        if(val.pkgname === deviceapp_val.package){
                                            // update address in deviceapp to show icon
                                            deviceAppArray[index].address = val.address;
                                
                                        }
                                    })
                                   
                                })
                            }else{
                                 console.error("data is not array")
                                 return;
                            }
                            this.thirdAppArray = deviceAppArray;
                        }
                    })
                    
                })
            },

             getSrc(raid){
                if(raid){
                    let repourl =  getRepoSrcApi();
                    return repourl+"/getAppIcon/"+raid;
                }else{
                    return 'static/imgs/default_apk.png';
                }
            },

            getSoftwareMonitorStatus(){
                if(!this.isSingleMode) {
                    console.error("Cannot operate in a batch mode");
                    return;
                }
                if(this.selectedAgentId == ""){
                    console.error("selectAgentId is empty");
                    return;
                }
                let param = "{'get_software_info':true}"
                this.softwareLoading = true;
                getDeviceStatus(this.selectedAgentId, param).then((obj) => {
                    handleResponse(obj, (res) => {
                        this.softwareLoading = false;
                        if(res.status == "CONTENT"){
                            let apppackageinfo = JSON.parse(res.content.value);
                            if(this.deviceType == this.groupTypeArray.android){
                                let thirdAppArray = [],systemAppArray = [], inMonitorArray = [];
                                let allAppData = apppackageinfo.data;
                                if(Object.prototype.toString.call(allAppData) === "[object Array]" && allAppData.length > 0){
                                    allAppData.forEach((val) => {
                                        let appObj = {
                                            appname: val.appName,
                                            package: val.packageName,
                                            versioncode: val.versionCode,
                                            versionname: val.versionName,
                                            ismonitor: val.isMonitor,
                                            address: "",
                                        };
                                        if(val.isMonitor === 'true'){
                                            inMonitorArray.push(val.packageName);
                                        };
                                        if(val.type === "third" && !disableAppArray.includes(val.packageName)){
                                            thirdAppArray.push(appObj);
                                        }else if(val.type === "system"){
                                            systemAppArray.push(appObj);
                                        }
                                    })
                                }else{
                                    console.error("get apps in agent error");
                                    return;
                                }
                                this.appOptions = systemAppArray;
                                this.appTableData = thirdAppArray; 
                                this.appInMonitor = inMonitorArray;
                                this.getRepoApps(thirdAppArray);
                            }else{
                                let monitorProcessData = [];
                                let processData = [];
                                apppackageinfo.forEach((item) => {
                                    if(item.threadismonitor === 'true'){
                                        monitorProcessData.push(item);
                                    }else{
                                        processData.push(item);
                                    }
                                })
                                
                                this.processData = processData;
                                this.monitorProcessData = monitorProcessData;
                            }   
                        }
                        
                    })   
                })
            },

            getDeviceThresholdSetting(){
                let param="{'get_threshold': true}"
                getDeviceStatus(this.selectedAgentId, param).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let thresholdInfo = JSON.parse(res.content.value);
                            if(thresholdInfo){
                                if(thresholdInfo.cpu_temp){
                                    if(thresholdInfo.cpu_temp.status === 'true'){
                                        this.cputemswitch = true;
                                    }else{
                                        this.cputemswitch = false;
                                    }
                                    this.cputemwarn = thresholdInfo.cpu_temp.warn;
                                    this.cputemerror = thresholdInfo.cpu_temp.error;
                                }
                                if(thresholdInfo.cpu_usage){
                                    if(thresholdInfo.cpu_usage.status === 'true'){
                                        this.cpuusageswitch = true;
                                    }else{
                                        this.cpuusageswitch = false;
                                    }
                                    this.cpuusagewarn = thresholdInfo.cpu_usage.warn;
                                    this.cpuusageerror = thresholdInfo.cpu_usage.error;
                                }
                                if(thresholdInfo.memory_usage){
                                    if(thresholdInfo.memory_usage.status === 'true'){
                                        this.memoryusageswitch = true;
                                    }else{
                                        this.memoryusageswitch = false;
                                    }
                                    this.memoryusagewarn = thresholdInfo.memory_usage.warn;
                                    this.memoryusageerror = thresholdInfo.memory_usage.error;
                                }
                                if(thresholdInfo.storage_usage){
                                    if(thresholdInfo.storage_usage.status === 'true'){
                                        this.storageusageswitch = true;
                                    }else{
                                        this.storageusageswitch = false;
                                    }
                                    this.storageusagewarn = thresholdInfo.storage_usage.warn;
                                    this.storageusageerror = thresholdInfo.storage_usage.error;
                                }
                                if(thresholdInfo.battery_available){
                                    if(thresholdInfo.battery_available.status === 'true'){
                                        this.batteryswitch = true;
                                    }else{
                                        this.batteryswitch = false;
                                    }
                                    this.batterywarn = thresholdInfo.battery_available.warn;
                                    this.batteryerror = thresholdInfo.battery_available.error;
                                }
                            }                     
                        }
                    })
                })   
            },

            initData(){
                this.appOptions=[];
                this.processData=[];
                this.monitorProcessData=[];
                this.monitorContainerData=[];
                this.keywords="";
                this.containerData=[];
                this.appTableData=[];
                this.cputem=false;
                this.cputemswitch=false;
                this.cputemwarn=0;
                this.cputemerror=0;
                this.cpuusage=false;
                this.cpuusageswitch=false;
                this.cpuusagewarn=0;
                this.cpuusageerror=0;
                this.memoryusage=false;
                this.memoryusageswitch=false;
                this.memoryusagewarn=0;
                this.memoryusageerror=0;
                this.storageusage=false;
                this.storageusageswitch=false;
                this.storageusagewarn=0;
                this.storageusageerror=0;
                this.battery=false;
                this.batteryswitch=false;
                this.batterywarn=0;
                this.batteryerror=0;
            },

            getDeviceAbility(){
                let param="{'get_ability': true}";
                getDeviceStatus(this.selectedAgentId, param).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let abilityInfo = JSON.parse(res.content.value);
                           
                            if(abilityInfo.cpu_temp ==='true'){
                                this.cputem = false;
                            }else{
                                this.cputem = true;
                            }
                            if(abilityInfo.cpu_usage ==='true'){
                                this.cpuusage = false;
                            }else{
                                this.cpuusage = true;
                            }
                            if(abilityInfo.memory_usage ==='true'){
                                this.memoryusage = false;
                            }else{
                                this.memoryusage = true;
                            }
                            if(abilityInfo.storage_usage ==='true' ){
                                this.storageusage = false;
                            }else{
                                this.storageusage = true;
                            }
                            if(abilityInfo.battery_available ==='true'){
                                this.battery = false;
                            }else{
                                this.battery = true;
                            }
                        }
                    })
                    this.getDeviceThresholdSetting();
                })
            },

            setMonitor(){
                if(this.selectedAgentId == ''){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')}).then(() =>{
                        this[cid] = this.$options.data()[cid];
                    })
                    return;
                }

                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("global.set") , this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) =>{
                    if (willfunc) {
                        let param;
                        if(this.activeName === "thresholdSetup"){
                            let storageusage = {'storage_usage':this.storageusageswitch,'warn':this.storageusagewarn,'error':this.storageusageerror}
                            let cpuusage={'cpu_usage':this.cpuusageswitch,'warn':this.cpuusagewarn,'error':this.cpuusageerror}
                            let cputem={'cpu_temp': this.cputemswitch,'warn':this.cputemwarn,'error':this.cputemerror};
                            let memoryusage={'memory_usage': this.memoryusageswitch,'warn':this.memoryusagewarn,'error':this.memoryusageerror}
                            let battery = {'battery_available':this.batteryswitch,'warn':this.batterywarn,'error':this.batteryerror};
                            
                            let set_threshold = [];
                            if(!this.cputem){
                                set_threshold.push(cputem);
                            }
                            if(!this.cpuusage){
                                set_threshold.push(cpuusage);
                            }
                            if(!this.memoryusage){
                                set_threshold.push(memoryusage);
                            }
                            if(!this.storageusage){
                                set_threshold.push(storageusage);
                            }
                            if(!this.battery){
                                set_threshold.push(battery);
                            }
                            let actions={
                                set_threshold: set_threshold,
                            }
                            param={
                                pluginname: pluginname,
                                actions: actions,
                            }
                        }else if(this.activeName === "softwareMonitor"){
                            let softwareData = this.appInMonitor;
                            if(softwareData.length==0){
                                param={
                                    pluginname: pluginname,
                                    actions: {"set_isMonitor": false},
                                    pkgname: softwareData,
                                }
                            }else{
                               let appArrayInfo = []
                               for(var i = 0; i < this.appInMonitor.length; i++){
                                    for(var j = 0; j < this.appOptions.length; j++){
                                        if(this.appInMonitor[i] === this.appOptions[j].package){
                                            var appInfo = new Object();
                                            appInfo.package = this.appOptions[j].package;
                                            appInfo.appname = this.appOptions[j].appname;
                                            appInfo.versionname = this.appOptions[j].versionname;
                                            appArrayInfo.push(appInfo); 
                                        }
                                    }
                                    for(var j = 0; j < this.appTableData.length; j++){
                                        if(this.appInMonitor[i] === this.appTableData[j].package){
                                            var appInfo = new Object();
                                            appInfo.package = this.appTableData[j].package;
                                            appInfo.appname = this.appTableData[j].appname;
                                            appInfo.versionname = this.appTableData[j].versionname;
                                            appArrayInfo.push(appInfo); 
                                        }
                                    }
                                }
                                param={
                                    pluginname: pluginname,
                                    actions: {"set_isMonitor": true},
                                    pkgname: appArrayInfo,
                                }
                            }
                        }else if(this.activeName === "processMonitor"){
                            param={
                                pluginname: pluginname,
                                actions: {"set_isProcessMonitor": true},
                                pkgname: this.multipleSelection,
                            }
                        }else if(this.activeName === "dockerMonitor"){
                            param={
                                pluginname: pluginname,
                                actions: {"set_isDockerMonitor": true},
                                pkgname: this.multipleContainerSelection,
                            }
                        }else{
                            console.error("set monitor error");
                            return;
                        }
                        
                        
                        this.softwareLoading = true;
                        setDeviceStatus(this.selectedAgentId, param).then((data) => {
                            this.softwareLoading = false;
                            handleResponse(data, (res) =>{
                                if(res.status === "CHANGED"){
                                    this.$swal("",this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        if(this.activeName === "thresholdSetup"){
                                            this.getDeviceThresholdSetting();
                                        }else if(this.activeName === "dockerMonitor"){
                                            this.getDockerStatus();
                                        }else{
                                            this.getSoftwareMonitorStatus();
                                        }
                                        
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })   
                        })
                    
                    }
                })
            },

            cancelMonitor(){
                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("global.cancel") , this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) =>{
                    if (willfunc) {
                        _g.openGlobalLoading();
                        let param;
                        if(this.activeName === "processMonitor"){
                            param={
                                pluginname: pluginname,
                                actions: {"set_isProcessMonitor": false},
                                pkgname: this.multipleSelection,
                            }
                        }else if(this.activeName === "dockerMonitor"){
                            param={
                                pluginname: pluginname,
                                actions: {"set_isDockerMonitor": false},
                                pkgname: this.multipleContainerSelection,
                            }
                        }else{
                            console.error('cancel monitor error');
                            return;
                        }
       
                        this.softwareLoading = true;
                        setDeviceStatus(this.selectedAgentId, param).then((data) => {
                            this.softwareLoading = false;
                            handleResponse(data, (res) =>{
                                if(res.status === "CHANGED"){
                                    this.$swal("",this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() =>{
                                        if(this.activeName === "processMonitor"){
                                            this.getSoftwareMonitorStatus();
                                        }else{
                                            this.getDockerStatus();
                                        }
                                    })
                                    
                                }else{
                                    _g.handleError(res);
                                }
                            })   
                        })
                        
                    }
                })
            },
             handleScroll(){
                let contentEle = document.getElementById("homeContant");
                var scrollTop = contentEle.pageYOffset || contentEle.scrollTop;
                if (scrollTop > this.offsetTop) {
                    this.isFixed = true
                } else {
                    this.isFixed = false
                }
            }
        },
        created() {
            if(this.$route.query.activename!=null){
                this.activeName = this.$route.query.activename;
            }
        },
        watch: {
            isSingleMode(val){
                if(val){
                    this.initData();
                }
            },

            selectedAgentId(val){
                this.initData();
                if(val){
                    if(((this.deviceType == this.groupTypeArray.linux|| this.deviceType == this.groupTypeArray.windows)&&this.activeName == "softwareMonitor")||
                    (this.deviceType == this.groupTypeArray.android&&(this.activeName == "processMonitor"|| this.activeName=="dockerMonitor"))
                    ){
                        this.activeName="thresholdSetup";
                    }
                    
                    this.getDeviceAbility();
                    this.getSoftwareMonitorStatus();
                    if(this.deviceType == this.groupTypeArray.linux|| this.deviceType == this.groupTypeArray.windows){
                        this.getDockerStatus();
                    }
                }
            },
            activeName(val){
                this.keywords="";
                if(val =="dockerMonitor"){
                    this.dockerIconColor="#409EFF"
                }else{
                    this.dockerIconColor="#626369"
                }
            }
        },
        computed: {
            thresholdSetupShow(){
                if(this.selectedAgentId){
                    return true;
                }else {
                    return false;
                }
            },

            cardAppData(){
                if(this.appType === "third"){
                    return this.appTableData;
                }else if(this.appType === "system"){
                    return this.appOptions;
                }else{
                    return [];
                }
            },
            
            appTypeShow(){
                if(this.cardAppData.length >0){
                    return true;
                }else{
                    return false;
                }
                
            },

            subIntro(){
                if(this.appType === "system"|| this.appType === "third"){
                    return ['appname', 'versionname'];
                }else{
                    return [];
                }
            },
            appTypeArray(){
                if(this.appType === "third"){
                    return ['third'];
                }else if(this.appType === "system"){
                    return ["system"];
                }else{
                    return []
                }
            },

            typeKey(){
                return "type"
            },

            isShowSearchInput(){
                if((this.activeName === "processMonitor"|| this.activeName== "dockerMonitor")
                && (this.deviceType == this.groupTypeArray.linux|| this.deviceType == this.groupTypeArray.windows)){
                    return true;
                }else{
                    return false;
                }
            },

            processTable(){
                this.multipleSelection = [];
                if(this.monitorType == "addMonitor"){
                    return this.processData.filter(data => !this.keywords || data.threadcmd.toLowerCase().includes(this.keywords.toLowerCase()));
                }else{
                    return this.monitorProcessData.filter(data => !this.keywords || data.threadcmd.toLowerCase().includes(this.keywords.toLowerCase()));
                }
            },

            containerTable(){
                this.multipleContainerSelection = [];
                if(this.monitorContainerType == "addMonitor"){
                    return this.containerData.filter(data => !this.keywords || data.name.toLowerCase().includes(this.keywords.toLowerCase()));
                }else{
                    return this.monitorContainerData.filter(data => !this.keywords || data.name.toLowerCase().includes(this.keywords.toLowerCase()));
                }
            },

            setBtnShow(){
                if(this.activeName == "softwareMonitor"||this.activeName == "thresholdSetup"||
                    (this.activeName == "processMonitor"&& this.monitorType == "addMonitor")||
                    (this.activeName == "dockerMonitor"&& this.monitorContainerType == "addMonitor")){
                    return true;
                }else{
                    return false;
                }
            },

            keywordsHolder(){
                if(this.activeName=='processMonitor'){
                    return this.$t('setting.processKeywordHolder');
                }else{
                    return this.$t('setting.containerKeywordHolder');
                }
            }
        },

        mounted() {
            let contentEle = document.getElementById("homeContant");
            this.offsetTop = document.getElementById("actionID").offsetTop;
            contentEle.addEventListener('scroll', this.handleScroll)
        },

        beforeDestroy() {
            let contentEle = document.getElementById("homeContant");
            contentEle.removeEventListener('scroll', this.handleScroll)
        },
        
    }
</script>

<style lang='scss' scoped>
@import "@/assets/css/colors.scss";
.pd-5{
    padding:5px
}
.counter{
    display: inline-block;
    width: 150px;
}
.info-box {
    display: block;
    min-height: 180px;
    background: #fff;
    width: 100%;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    border-radius: 3px;
    margin: 15px 0px 15px 0px;;
    text-align: center;
}
.info-box-icon {
    border-radius: 50%;
    display: block;
    /* float: left; */.p{
        margin-top: 20px; 
    }
    .i{
        margin-top: 20px;
        padding-right: 30px;
        width: 150; 
    }
    .span{
        display: block;
        float: left;
    }
    height: 120px;
    width: 120px;
    
    line-height: 40px;
    font-size: 45px;
    /* background: rgba(0, 0, 0, 0.2); */
    margin-top: 15px;
    margin:1rem auto;
    i{
        margin-top: 20px;
    }
}
.info-box-icon-white {
  color: #fff;
}
.info-box-icon-title{
	color:#fff;
    font-size:15px;
    margin-bottom: 20px;
}
.setting-title{
    margin-top: 10px;
    margin-left:5px;
    font-size: 16px;
}
.setting-body{
    margin-top: 15px;
    display: flex;
    flex-direction: row;
    font-weight: 600;
    font-size: 16px;
    div{
        width: 50%;
        text-align: center;
        span{
            display: inline-block;
            margin-bottom: 10px;
            min-height: 30px;
        }
    }

}
.border-style{
    padding-left: 10px;
    padding-right: 10px;
    display: inline-block;
    min-height: 200px;
    max-width:420px;
    -moz-box-shadow: 0 3px 12px 0 rgba(0,0,0,.1);
    -webkit-box-shadow: 0 3px 12px 0 rgba(0,0,0,.1);
    box-shadow: 0 3px 12px 0 rgba(0,0,0,.1);
    border: 1px solid #dfdfdf;
    margin-left: 5px;
    margin-top: 15px;
    // background-color: #e2f5fb;
}
.warn-word{
    font-weight: 500;
    margin-top:10px;
    span{
        display: inline-block;
        margin-top: 10px;
        margin-left: 5px;
    }
}
.save-all{
    .el-button{
        position: absolute;
        top: 0px;
        right:20px;
        z-index:10001;
    }
}

.card-header{
    input[type="checkbox"]{
        position: relative;
        top: 0;
        zoom:150%;
        cursor: pointer;
        border: 0;
    }
}

.noData{
    margin-top: 30px;
    p{
        img{
            width: 27px;
            vertical-align: middle;
        }
        margin-bottom: 20px;
        color: #bfbfbf;
        font-size: 21px;
    }
}

.action-icon{
    position: relative;
    top: 1px;
}

.btnGroup{
    padding: 10px;
    background: #fff;
    margin: 10px 0;
    border-radius: 5px;
}

.dockerIcon{
    position: relative;
    top: 3px;
}

.search{
    position: absolute;
    top: 21px;
    right: 25px;
    width: 230px;
    z-index: 1001;
    
    .search-icon{
        position: absolute;
        top: 6px;
        right: 1px;
        color: #ddd;
        z-index: 10001;
    }
}
</style>



