            let validateTaskname = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[\u4E00-\u9FA5A-Za-z0-9_\-\.\s]{1,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            return {
                activeName: "batchThresholdSetup",
                cputem: false,
                cputemswitch: false,
                cputemwarn: 0,
                cputemerror: 0,
                cpuusage: false,
                cpuusageswitch: false,
