        font-size: 21px;
    }
}
.action-icon{
    position: relative;
    top: -1px;
}
.btnGroup{
    padding: 10px;
    background: #fff;
    margin: 10px 0;
    border-radius: 5px;
}

.dockerIcon{
    position: relative;
    top: 3px;
}

.search{
    position: absolute;
    top: 21px;
    right: 25px;
    width: 230px;
    z-index: 1001;
    
    .search-icon{
        position: absolute;
        top: 6px;
        right: 1px;
        color: #ddd;
        z-index: 10001;
    }
}
</style>



